
html, body, div, header, ul, li, h1, h2, h3, p{
  margin: 0;
  padding: 0;
}

@font-face{
  font-family: "GowunDodum";
  font-weight: 400;
  src: url("fonts/GowunDodum-Regular.ttf") format("truetype");
}

a:link{
  color: #000;
  text-decoration: none;
}
a:visited{ 
  color: #000;
  text-decoration: none;
}

a:hover{
  color: #CC3D3D;
  text-decoration: none;
}

#introduce, #movies, #reservation, #find, #event, #moive{
  width: 960px;
  text-align: center;
}

ul{
  list-style: none;
}

#wrap{
  width: 960px;
  background-image: linear-gradient(to bottom, #EAEAEA 0%,#fff 50%, #e6e6e6 100%);
  margin: 0 auto;
  font-family: "GowunDodum";
}

.logo{
  width: 200px;
  display: block;
  text-align: center;
}

.visual{
  width: 960px;
}

#wrap>ul{
  display: flex;
  justify-content: space-evenly;
  margin: 30px 0;
  align-items:center;
}

#wrap>ul>li{
  flex: row;
  display: inline-block;
  margin: 10px 10px;
  background-color: #FFA648;
  font-size: 1.2em;
  font-weight: bold;
  padding: 10px 0px;
  border-radius: 20px;
  flex-basis: 150px;
  text-align: center;
}

#wrap>ul>li>p{
  margin: 10px;
}
#wrap h1{
  text-align: center;
}
#wrap h2{
  width: 500px;
  display: inline-block;
  text-align: center;
  margin: 0 auto;
}

#wrap h3{
  font-size: 1.3em;
}

#movies>ul{
  width: 960px;
  height: 100px;
  margin-top: 20px;
}


#movie{
  background-image: linear-gradient(to bottom, #333333 0%,#D5D5D5 50%, #333333 100%);
  color: #fff;
}

#movie h3{
  margin-top: 40px;
  text-align: center;
  letter-spacing: 3px;
  background-image: linear-gradient(to bottom right, #BC602B 0%,#E0844F 50%, #FFBA85 100%);
  background-color: #BC602B;
  color: #000;
  padding: 20px;
  font-size: 1.5em;
}

#movie h3::before, #movie h3::after{

  content: " â˜… ";
  color: #630200;
}

.movieList{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.movieList img{
  border: 1px solid;
  width: 310px;
  height: 500px;
  box-shadow: 9px 7px 10px rgba(0, 0, 0, 0.7);
}

#movie > .movieList > div{
  flex: row;
  margin: 50px 70px;
  width: 300px;
}

.movieList p{
  width: 319px;
  font-size: 1.4em;
  font-weight: bold;
  margin-bottom: 15px;
  text-shadow: 5px 4px 3px rgba(0, 0, 0, 0.9);
  letter-spacing: 5px;
}

.movieList span{
  display: inline-block;
  width: 312px;
  font-weight: bold;
  font-size: 1.2em;
  margin-top: 15px;
  background-color: #000;


}

.movieTitle{
  float: left;
  width: 100px;
  margin: 20px 20px;
  position: relative;
  left: 85px;
  display: block;
  font-size: 1.3em;
  font-weight: bold;
  background-color: #F29661;
  padding: 10px 30px;
  border-radius: 20px;
}

.movieTitle:hover{
  text-decoration: underline;
}

#reservation, #find, #event{
  width: 960px;
  padding-bottom: 50px;
}

#reservation>ul, #find>ul, #event>ul{
  margin: 20px 20px;
  font-size: 1.3em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.reTitle, .findTitle, .eventTitle{
  width: 200px;
  margin: 20px 20px;
  font-weight: bold;
  background-color: #F29661;
  padding: 10px 30px;
  border-radius: 20px;
  flex: row;
  
  
}